.completed{
    color: $lighterColor2
}
.current_word{
    background-color: $lighterColor2;
    padding: $padding-l3;
    border-radius: 3%;
}

.typing-text{
    letter-spacing: 3px;
    font-size: 24px;
    line-height: 1.5;
    margin-top: 0;
    display: flex;
    flex-wrap: wrap;
}

.typing-text-field{
    width: 100%;
}

.typing-panel{
    border: $borderStyle;
    background: white;
    position: relative;
}
.typing-part{
    padding: $padding-l1;
}

.completed-word{
    padding: $padding-l3;
    color: $lighterColor2
}
.uncompleted-word{
    padding: $padding-l3;
}
.completed-green-word{
    padding: $padding-l3;
    color: #0eb472;
}

.result-panel{
    display: grid;
    grid-template-columns: 62% 38%;
    border-bottom: $borderStyle;
    padding: $padding-l1;
    padding-top: $padding-l2;
    padding-bottom: $padding-l2;
}
.result-panel > div{
    text-align: left;
    line-height: 2;
    margin: auto;
    margin-left: 0;
}
.wpm{
    text-align: center;
}
.accuracy{
    text-align: center;
}

.result-label{
    margin: 0;
    padding-left: $padding-l1;
    padding-bottom: $padding-l2;
    padding-top: $padding-l2;
    color: $darkColor1;
    border-bottom: $borderStyle;
}
.result-1{
    border-right: $borderStyle;
}

.lesson-header{
    padding-left: $padding-l3;
    border-left: 8px solid $selectedLessonColor
}
.module-header{
    border-left: 8px solid $selectedModuleColor;
    padding-left: $padding-l3;
}

.above-goal .MuiSlider-track, .above-goal .MuiSlider-thumb{
background-color: #75d62a;
}
.middle-to-goal .MuiSlider-track, .above-goal .MuiSlider-thumb{
    background-color: #d6b62a;
}
.low-to-goal .MuiSlider-track, .above-goal .MuiSlider-thumb{
    background-color: #d6352a;
}
.MuiSlider-thumb{
   visibility: hidden;
}


.MuiSlider-markLabel{
    font-size: ms(0) !important;
}
.goal-slider{
    height: 100%;
    display: flex;
    align-items: center;
}

.typing-complete{
    background-color: white;
    width: 100%;
    height: 100%;            
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0.95;
    z-index: 99;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.typing-complete > div{
    margin: $padding-l1;
    font-size: ms(3);
}