.hotkey-lesson-header{
    padding-left: $padding-l3;
    border-left: 8px solid $currentHotkeyColor;
}

.hotkey-text{
    font-size: 24px;
    line-height: 1.5;
    margin-top: 0;
    display: flex;
    flex-wrap: wrap;
    color: $lightColor2;
}

.hotkey-combination-button{
    border: $borderStyle;
    padding-left: $padding-l3;
    padding-right: $padding-l3;
    font-family: Helvetica, Arial, sans-serif;
    background: white;
    border-radius: 5px;
    box-shadow: 1px 1px 5px white inset;
    //border-width: 2px;
    color: black;
    max-height: 20px;
    min-height: 20px;;
    vertical-align: bottom;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    
}
.hotkey-instructions{
    display: grid;
    //grid-template-columns:  25% 20% 28% 23%;
    //grid-template-rows:  49% 49%;
    grid-template-columns:  24% 29%;
    grid-template-rows:  16px 16px 16px 16px;
    row-gap: $padding-l2;
    column-gap: 47%;
    grid-auto-flow: column;
    border-top: $borderStyle;
    padding: $padding-l1;
}
.hotkey-circle{

    padding: 2px; 
    border-radius: 50%;
    display: inline;

}
.hotkey-description{
    color: $darkColor1 !important;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 12px;
}

.hotkey-text-field{
    padding: $padding-l1;
    padding-bottom: 0;
}