.lessons-mode{
    //border-bottom: $borderStyle;
    display: grid;
    grid-template-rows: 28% 5% 67%;
    height: 100%;
}
.lessons-container{
    padding: $padding-l2;
    //padding-top: $padding-l0;
    //padding-bottom: $padding-l0;
    justify-content: center;
    display: flex;
    align-items: start;
    flex-wrap: wrap;
    align-content: space-between;
}
.lessons-mode .lessons-select-header h2 {
    margin: 0;
    text-align: center;
    color:  $darkestColor;
}
.lessons-mode .module-select-header h2 {
    margin: 0;
    text-align: center;
    color: $darkestColor;
}
.lessons-select-header{

}
.module-select-header{

}
.module-select div{
    border-radius: 50%;
    //width: 30px !important
}
.module-select {
    //width: 50% !important;
}
.lessons-select{
    display: inline-flex;
    flex-wrap: wrap;
    width: 80%;
    justify-content: center;
}
.lessons-select-keyboard div{
    width: 80% !important;
    margin-bottom: $padding-l1 !important;
    font-size: ms(1) !important;
}
.lessons-select-keyboard {
    width: 80%;
    justify-content: center;
}
.lessons-select div{
    text-align: center;
    padding: 4px;
    margin: 1%;
    width: 56px;
    height: 56px;
    line-height: 55px;
    background-color: $lightestColor;
    color: $lightColor2;

}
.lessons-block {
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.lessons-style-select{
    display: inline-flex;
    align-items: center;
    width: 74%;
    justify-content: center;
} 
.lessons-style-select > .MuiFormControl-root {
    width: 100%;
    text-align: center;
}
.lessons-label{
    margin-right: $padding-l1;
}
.current-lesson{
    background-color: $selectedLessonColor !important;
    color: $darkestColor !important;
    cursor: pointer;
}
.current-module{
    background-color: $selectedModuleColor !important;
    color: $darkestColor !important;
    cursor: pointer;
}
.unlocked-lesson{
     background-color: $lessonColor !important; //#85bee4 !important;
     color: white !important;
     cursor: pointer;
}
.unlocked-module{
    background-color: $moduleColor !important;//#9bae0d !important;
    color: white !important;
    cursor: pointer;
}

.shortcut-lesson-tile{
    background-color: $hotkeyeColor  !important;
    color: white !important;
    cursor: pointer;
}
.current-shortcut-lesson-tile{
    background-color: $currentHotkeyColor  !important;
    color: $darkestColor !important;
    cursor: pointer;
}

// SELECT FILTERS MODE
.selected-lesson-type {
    //background: white !important;
    background-color: $lightestColor !important;
  }
 .select-lesson-type > div {
    @include align-inner-element-center-vertically;
    -webkit-flex: 1;
    flex: 1;
    text-align: center;
    font-size: ms(1);
    color: $darkerColor1;
    background: $lighterColor2;
    cursor: pointer;
    transition: background-color 0.16s;
    transition-timing-function: cubic-bezier(0.4, 0, 0.6, 1);
    font-weight: 600;
    //border-bottom: $borderStyle;
  }
  
  .select-lesson-type > div:hover {
    background-color: $lightestColor !important;
    //border-bottom: $borderStyle;
  }
  .select-lesson-type > div:nth-last-child(1) {
    //border: none;
  }
  .select-lesson-type {
    display: flex;
    height: 48px;
    width: 100%;
    margin: auto;
    border: $borderStyle;
  }