
$challengeColor: #5b6a48;
$currentChallengeColor: #ffffff;


.challenge-select{
    display: inline-flex;
    flex-wrap: wrap;
    //width: 82%;
    justify-content: center;
    margin-bottom: $padding-l2;
}

.challenge-select > div{
    text-align: center;
    padding: 4px;
    margin-top: 16px;
    width: 50%;
    //height: 56px;
    line-height: 36px;
    background-color: $lightestColor;
    color: $lightColor1;
}



.current-challenge{
    background-color: $currentChallengeColor !important;
    color: $darkestColor !important;
    cursor: pointer;
    font-size: ms(1);
    border: 1px solid $challengeColor; 
}
.challenge{
    background-color: $challengeColor !important;
    color: white !important;
    cursor: pointer;
    font-size: ms(1);
    //text-shadow: black 1px 1px 1px;
}
.challenge-star{
    color: #f1ab09 !important;
}
.challenge-star-locked{
    color: #00000080 !important;
}
.challenge-header{
    padding-left: $padding-l3;
    border-left: 8px solid $currentChallengeColor;
}

.challenge-star-description{
    display: flex;
    align-items: center;
    text-align: center; 
    width: 53%;
    margin: auto;
    font-size: ms(0);
}

.challenge-select-header{
    margin: 0;
    text-align: center;
    color:  $darkestColor;
    margin-top: $padding-l1;
    //margin-bottom: $padding-l3;

}