@import "./variables";
@import "./medias";
@import "./lessons-select.scss";
@import "./typing-panel.scss";
@import "./challenge.scss";
@import "./right-panel.scss";
@import "./hotkey-panel.scss";
@import "./cursor.scss";
@import "./race.scss";

.react-swipeable-view-container {
  height: 100%;
}
.unselectable {
  user-select: none;
  -webkit-touch-callout: none;
}
.MuiSlider-root {
  color: $lightColor1 !important;
}
.apply-color-title {
  background: $darkColor2;
  color: white;
  text-align: center;
}
.App {
  height: $fill;
  grid-template-rows: 5% 95%;
  display: grid;
}
#root {
  height: 100%;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  color: $darkerColor2;
  font-weight: 400 !important;
}
html {
  font-family: Roboto, serif;
}
body {
  height: 100%;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1;
  font-weight: 400 !important;
  color: $darkerColor2 !important;
  font-family: "Roboto", sans-serif;
  font-size: ms(-1);
}
footer {
  background: $darkerColor1;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.header {
  @include align-inner-element-center-vertically;
  //padding-left: $padding-l1;
  background: $darkerColor1 !important;
}
.header > h2 {
  margin: 0;
  color: $lighterColor2;
  font-weight: 400;
  letter-spacing: 0.05em;
}
#menu-button {
  margin-right: $padding-l2;
}
// RIGHT PART
.left-panel {
  //border-top: $borderStyle;
  background-color: white
}
.filters-long-part {
  display: grid;
  height: 100%;
}


.selected-mode {
  //background: white !important;
  background-color: $lighterColor2 !important;
}
.select-mode-table > div {
  @include align-inner-element-center-vertically;
  -webkit-flex: 1;
  flex: 1;
  text-align: center;
  font-size: ms(1);
  color: $darkerColor1;
  background: $lighterColor1;
  cursor: pointer;
  transition: background-color 0.16s;
  transition-timing-function: cubic-bezier(0.4, 0, 0.6, 1);
  font-weight: 600;
}

.select-mode-table > div:hover {
  background-color: $lighterColor2 !important;
}
.select-mode-table > div:nth-last-child(1) {
  border: none;
}
.select-mode-table {
  display: flex;
  height: 48px;
}



// TILES

#panel-container {
  border-left: $borderStyle;
  border-right: $borderStyle;
  border-top: $borderStyle;
  display: grid;
  background-color: $lightestColor;
  color: $darkestColor;
  // in medias
  //grid-template-columns: 17% 83%;
}
#panel-container > div:nth-child(2) {
  height: -webkit-fill-available;
  overflow: auto;
  display: flex;
  align-items: center;
}
#tiles-holder {
  width: 100%;
}
.tiles-actions-bar {
  @include btn-icon-color;
  border-right: $borderStyle;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: $lightestColor;
}
.tiles-actions-bar > div {
  margin-bottom: $padding-l1;
  margin-top: $padding-l1;
}
.tiles-actions-bar2 > div {
  @include align-inner-element-center-vertically;
}
.arrow-btn {
  cursor: pointer;
  display: inline-block;
}
.arrow-btn:hover {
  @include btn-icon-color-hover;
}
.arrow-btn:first-child {
  margin-right: $padding-l2;
}

.tiles-section {
  display: table;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
}
.tiles-section >div {
  width: 100%;
}

// TRY PART

.selected-color-panel {
  width: 150px;
  height: fit-content;
  padding: $padding-l3 $padding-l2;
  text-align: center;
  display: grid;
  font-size: 1em;
  cursor: pointer;
  border-radius: $radius;
}

.horizontal-holder-sl {
  display: grid;
  grid-template-columns: $short $long;
  height: 100%;
}
.horizontal-holder-ls {
  display: grid;
  // in media
  //grid-template-columns: 72% 28%;
  height: 100%;
}
.vertical-holder-sl {
  height: 100%;
  display: grid;
  grid-template-rows: $short $long;
}
.vertical-holder-ls {
  height: 100%;
  display: grid;
  grid-template-rows: $long $short;
}

/* SINGLE COMPONENTS */

.export-json-btn {
  font-size: 1em;
  cursor: pointer;
}
.export-json-btn:hover {
  @include btn-icon-color-hover;
}

.tools-section{
  background-color: $lightestColor;
}

.middle{
  //display: grid;
  //grid-template-rows: 5% 95%;
}

// Profile
.profile-block{
  text-align: left;
  display: grid;
  grid-template-columns: 40% 60%;
  padding: $padding-l2;
  background-color: white;
  border-bottom: $borderStyle;
}
.profile-stats div{
  padding-top: $padding-l4;
  padding-bottom: $padding-l4;
}
.profile-img-block{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
}
.profile-img{
  padding: 25%;
  border-radius: 50%;
  background-color: $lightColor;
}

.user-account{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}