.race-cars{
    border-bottom: $borderStyle;
    padding: $padding-l1;
    padding-top: $padding-l2;
    padding-bottom: $padding-l2;
}
.user-track{
    padding: $padding-l1;
    padding-top: $padding-l2;
    padding-bottom: $padding-l2;
}
.guest-user-block{
    margin-bottom: $padding-l1;
    width: 68%;
    display: grid;
    grid-template-columns: 32% 68%;
}
.guest-users-panel{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 80%;
}